input[type="text"],
input[type="email"],
input[type="password"],
input[type="search"] {
  -webkit-appearance: none;
  background: none;
  border: 1px solid #dedede;
  border-radius: 1rem;
  box-shadow: none;
  color: $color-dark;
  font-size: 0.8rem;
  height: 1rem;
  line-height: 1rem;
  outline: none;
  padding: 1rem;
  width: 240px;
}
